/* label underline focus color */
.input-field input[type=text]:focus {
    border-bottom: 1px #e57373 !important;
    box-shadow: 0 1px 0 0 #e57373 !important;
}

.input-field input[type=email]:focus {
    border-bottom: 1px #e57373 !important;
    box-shadow: 0 1px 0 0 #e57373 !important;
}

.input-field input[type=password]:focus {
    border-bottom: 1px #e57373 !important;
    box-shadow: 0 1px 0 0 #e57373 !important;
}

.input-field textarea:focus {
    border-bottom: 1px #e57373 !important;
    box-shadow: 0 1px 0 0 #e57373 !important;
}

/* label underline focus color */
.input-field input .invalid[type=text]:focus {
    border-bottom: 1px #FF0000 !important;
    box-shadow: 0 1px 0 0 #FF0000 !important;
}

.input-field input .invalid[type=email]:focus {
    border-bottom: 1px #FF0000 !important;
    box-shadow: 0 1px 0 0 #FF0000 !important;
}

.input-field input .invalid[type=password]:focus {
    border-bottom: 1px #FF0000 !important;
    box-shadow: 0 1px 0 0 #FF0000 !important;
}

