/* Loader */
.loader {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
}

/* Landing Page */

.landing {
    position: relative;
    background: url('https://wallpapermemory.com/uploads/705/budapest-background-hd-1920x1080-479639.jpg') no-repeat;
    background-size: cover;
    background-position: center;
    height: 100vh;

}

.dark-overlay {
    background-color: rgba(0, 0, 0, 0.7);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.landing-col {
    margin-top: 100px;
}

.landing-title {
    /* font-family: 'Caveat', cursive; */
}

.landing-hr {
    width: 80%;
}

